import { defineComponent, reactive, onBeforeMount } from 'vue';
import $store from '../../store/index.js';
// 组件
import VHeader from "../../components/Header/Header";
import UniTabs from '../../components/uni-tabs/uni-tabs.vue';
import UniMap from '../../components/uni-map/uni-map.vue';
import UniTab1 from '../../components/uni-tabs/tab1/tab1.vue';
import UniTab2 from '../../components/uni-tabs/tab2/tab2.vue';
export default defineComponent({
    name: 'Index',
    components: {
        VHeader: VHeader,
        UniTabs: UniTabs,
        UniMap: UniMap,
        UniTab1: UniTab1,
        UniTab2: UniTab2
    },
    setup: function (props) {
        var state = reactive({
            title: '首页',
            tabs: [{
                    name: '123'
                }, {
                    name: '234'
                }],
            defaultIndex: 0
        });
        var tabClick = function (current) {
            state.defaultIndex = current;
        };
        // 获取用户信息
        onBeforeMount(function () {
            // 获取系统信息
            uni.getSystemInfo({
                success: function (res) {
                    $store.commit('setSystem', res);
                }
            });
            // 获取微信信息
            uni.getUserInfo({
                provider: "weixin",
                success: function (res) {
                    console.log(res);
                }
            });
            // 校验是否登录
            uni.request({
                url: "/test/login",
                method: "GET",
                success: function (res) {
                    console.log(res);
                },
                fail: function (err) {
                    console.log(err);
                    // toLogin();
                }
            });
        });
        // 登录
        var toLogin = function () {
            uni.navigateTo({
                animationType: "auto",
                url: '../login/login'
            });
        };
        return {
            state: state,
            props: props,
            tabClick: tabClick,
            toLogin: toLogin
        };
    }
});
